'use client';

import { useEffect, ReactNode, useState } from "react";
import { useRouter } from "next/navigation";
import { usePathname } from "next/navigation";
import { useSession, signOut } from "next-auth/react";
import PageLoader from "@/components/ui/PageLoader";
import { onboardingAPI } from "@/lib/api";

// app/(protected)/layout.tsx
export default function ProtectedLayout({ children }: { children: ReactNode }) {
    // T·∫•t c·∫£ hook ph·∫£i ·ªü ƒë·∫ßu function
    const router = useRouter();
    const pathname = usePathname();
    const { data: session, status } = useSession();
    const [onboardingStatus, setOnboardingStatus] = useState<any>(null);
    const [isCheckingOnboarding, setIsCheckingOnboarding] = useState(false);

    console.log('üè† Protected Layout:', {
        status,
        hasSession: !!session,
        pathname,
        userEmail: session?.user?.email,
        onboardingStatus: onboardingStatus?.isOnboarded
    });

    // Check onboarding status
    const checkOnboardingStatus = async () => {
        if (!session?.user) return;
        
        // ‚úÖ Skip onboarding check if user is editing onboarding
        const urlParams = new URLSearchParams(window.location.search);
        const isEditing = urlParams.get('edit') === 'true';
        
        if (isEditing && pathname === '/onboarding') {
            console.log('üìù User editing onboarding, assuming onboarded');
            setOnboardingStatus({ isOnboarded: true });
            return;
        }
        
        try {
            setIsCheckingOnboarding(true);
            console.log('üîç Checking onboarding status...');
            const response = await onboardingAPI.getStatus();
            
            if (response.success) {
                setOnboardingStatus(response.data);
                console.log('‚úÖ Onboarding status:', response.data);
            }
        } catch (error: any) {
            console.error('‚ùå Error checking onboarding status:', error);
            
            // ‚úÖ Handle 401 gracefully - don't auto-logout for edit mode
            if (error?.response?.status === 401) {
                if (isEditing && pathname === '/onboarding') {
                    console.warn('üö´ Token expired but user editing onboarding, allowing access');
                    setOnboardingStatus({ isOnboarded: true });
                } else {
                    console.warn('üö´ Token invalid or expired, logging out...');
                    signOut({ callbackUrl: '/sign-in' });
                    return;
                }
            } else {
                // N·∫øu l·ªói kh√°c th√¨ assume user ch∆∞a onboarded
                setOnboardingStatus({ isOnboarded: false });
            }
        } finally {
            setIsCheckingOnboarding(false);
        }
    };

    useEffect(() => {
        if (status !== 'authenticated') return;
        if (session?.user && !onboardingStatus && !isCheckingOnboarding) {
            checkOnboardingStatus();
        }
    }, [status, session, router, pathname, onboardingStatus, isCheckingOnboarding]);

    let content = null;
    if (status === 'loading' || isCheckingOnboarding) {
        content = <PageLoader />;
    } else if (status === 'unauthenticated' || !session) {
        console.log('‚ùå No session, redirecting to sign-in');
        router.push("/sign-in");
        content = <PageLoader />;
    } else if (session?.user && onboardingStatus && !onboardingStatus.isOnboarded && pathname !== '/onboarding') {
        console.log('üìù User not onboarded, redirecting to onboarding');
        router.push('/onboarding');
        content = <PageLoader />;
    } else if (session?.user && onboardingStatus?.isOnboarded && pathname === '/onboarding') {
        // Check if user is trying to edit onboarding (allow access)
        const urlParams = new URLSearchParams(window.location.search);
        const isEditing = urlParams.get('edit') === 'true';
        
        if (!isEditing) {
            console.log('‚úÖ User already onboarded, redirecting to home');
            router.push('/');
            content = <PageLoader />;
        } else {
            console.log('üìù User editing onboarding, allowing access');
            content = <>{children}</>;
        }
    } else {
        content = <>{children}</>;
    }

    return content;
}